import { Component, OnInit, EventEmitter, Output, Input } from '@angular/core';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Message } from 'primeng/primeng';
import { TempImportApplyBean } from '../bean/temp-import-apply.bean';
import { BusinessCenterManageHttp } from '../../http/business-center-manage.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { TZB_COMMON_UPLOAD } from 'app/@uisftech/common/service/http.constant';

@Component({
  selector: 'temp-import-apply-update',
  templateUrl: './temp-import-apply-update.component.html',
  styleUrls: ['./temp-import-apply-update.component.scss']
})
export class TempImportApplyUpdateComponent implements OnInit {
  UPLOAD: string;
  fileReponse: any;
  fileList: any;
  files: any[] = [];
  commonHeader: any;
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  @Input() inValue: any;
  Approvalstate: any;
  code: any;
  ch: any = CALENDAR_CN;//讲英文转化为中文
  msgs: Message[] = [];//信息提示框
  tempImportApplyBean: TempImportApplyBean = new TempImportApplyBean();
  user2: any;
  constructor(
    public httpService: BusinessCenterManageHttp,
    public commfunc: Commfunc,
  ) {
    this.codeValues();
    this.Approvalstate = this.code['Approvalstate'];//界面展示形式
  }

  ngOnInit() {
    this.commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    this.tempImportApplyBean = this.inValue;
    let path = `BusinessCenter/TempImportApply/${this.commfunc.transDate(new Date())}/${this.commonHeader['userId']}`;
    this.UPLOAD = `${TZB_COMMON_UPLOAD}?path=${path}`;
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  load(event) {
    this.files.push(...event.files);
    if (event.xhr.response) {
      this.fileList = JSON.parse(event.xhr.response);
      this.fileReponse = event.xhr.response;
      if(this.fileList.hasOwnProperty('errorMsg')){
        this.msgs = [{severity:'error',summary:'提示',detail:this.fileList.errorMsg}];
      }
    } else {
      this.msgs = [{ severity: 'error', summary: '提示', detail: '获取服务器列表失败！' }];
    }
  }
  //上传保存
  uploadSave() {
    this.loadFunc('SAVE', '保存成功');
  }
  //上传提交
  uploadSubmit() {
    this.loadFunc('SUBMIT', '提交成功');
  }
  //上传取消
  uploadCancel() {
    this.outValue.emit({ display: false });
  }
  loadFunc(flag, msg) {
    let param = {
      flag,
      seqNo: this.inValue.seqNo,
      reason: this.inValue.reason,
      tellerId: this.inValue.tellerId,
      orgId: this.inValue.orgId,
      fileName: JSON.parse(this.fileReponse)[0].fileName,
      filePath: JSON.parse(this.fileReponse)[0].filePath,
      tellerName: this.user2['userName'],
      orgName: this.user2['orgName'],
      operateType: '1',
      externalIdType: 'PERMISSION'
    }
    this.httpService.createOrUpdatePermissionChangeReq(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: "上传成功!" }];
        this.outValue.emit({ display: false, msg });
      } else {
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, error => {
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
    })
  }
  modifyCloseBtn() {
    this.outValue.emit({ display: false });
  }

}
