import { Component, OnInit } from '@angular/core';
import { ConfirmationService, Message } from 'primeng/primeng';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';
import { BusinessCenterManageHttp } from '../http/business-center-manage.http.service';
import { TempImportApplyBean } from './bean/temp-import-apply.bean';
import { TZB_COMMON_UPLOAD, TZB_COMMON_DOWNLOAD } from "app/@uisftech/common/service/http.constant";

@Component({
  selector: 'temp-import-apply',
  templateUrl: './temp-import-apply.component.html',
  styleUrls: ['./temp-import-apply.component.scss']
})
export class TempImportApplyComponent implements OnInit {
  DOWNLOAD: string = TZB_COMMON_DOWNLOAD;
  commonHeader: any;
  fileReponse: any;
  loadDisplay: boolean;
  templateInfo: any;
  fileList: any;
  files: any[] = [];
  tableData: any;
  total: any;
  Approvalstate: any;
  code: any;
  first: number = 0;
  tempImportApplyBean: TempImportApplyBean = new TempImportApplyBean();
  headerTitle: string;
  display: boolean;//弹框
  updateValue: string;//修改传值
  showModel: string;
  ch: any = CALENDAR_CN;//讲英文转化为中文
  msgs: Message[] = [];//信息提示框
  UPLOAD: string;
  chooseDisplay: boolean;
  choosePer: boolean;
  chooseOrg: boolean;
  startTime: any;
  endTime: any;
  constructor(
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc,
    public httpService: BusinessCenterManageHttp
  ) {
    this.codeValues();
    this.Approvalstate = this.code['APPRVETemplate'];//审批状态
  }

  ngOnInit() {
    this.tempImportApplyBean.pageNum = 1;
    this.tempImportApplyBean.pageSize = 10;
    this.query();
    this.commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));

  }

  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //申请人
  openApplyPerson() {
    this.chooseDisplay = true;
    this.choosePer = true;
  }
  choosePerson(event) {
    this.tempImportApplyBean.tellerName = event[0].tellerName;
    this.tempImportApplyBean.tellerId = event[0].tellerId;
    this.chooseDisplay = false;
    this.choosePer = false;
  }
  //申请人机构
  openApplyOrg() {
    this.chooseDisplay = true;
    this.chooseOrg = true;
  }
  chooseOrgs(event) {
    //console.log(event)
    this.tempImportApplyBean.orgId = event.orgId;
    this.chooseDisplay = false;
    this.chooseOrg = false;
  }
  selectTime1() {

  }
  //查询
  query() {
    this.tempImportApplyBean.startTime = this.commfunc.transDateN(this.startTime);
    this.tempImportApplyBean.endTime = this.commfunc.transDateN(this.endTime);
    this.httpService.queryPermissionChangeReqInfo(this.tempImportApplyBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.total = data.total;
        this.tableData = data.configInfoList ? data.configInfoList : [];
        this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
      } else {
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, error => {
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
    })
  }
  //点击查询
  queryClick() {
    this.tempImportApplyBean.pageNum = 1;
    this.tempImportApplyBean.pageSize = 10;
    this.first = 0;
    this.query();
  }
  //导入
  import() {
    this.headerTitle = '用户审批权限管理新增';
    this.display = true;
    this.showModel = '1';
  }
  //下载
  download(item) {
    if (item.fileName && item.filePath) {
      window.open(this.DOWNLOAD + "?fileName=" + encodeURI(item.fileName) + "&fileUrl=" + item.filePath, "_self")

    }

  }
  //重置
  reset() {
    this.tempImportApplyBean = new TempImportApplyBean();
    this.startTime = '';
    this.endTime = '';
  }


  //新增回调
  addCall(event) {
    if (event.msg) {
      this.msgs = [{ severity: 'error', summary: '提示', detail: event.msg }];
      this.query();
    }
    this.display = event.display;
  }
  //修改
  update(item) {
    this.headerTitle = '用户审批权限管理修改';
    this.display = true;
    this.showModel = '2';
    this.updateValue = item;
  }
  //修改回调
  updateCall(event) {
    if (event.msg) {
      this.msgs = [{ severity: 'error', summary: '提示', detail: event.msg }];
      this.query();
    }
    this.display = event.display;
  }
  //删除
  delete(item) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        // 条件
        let param = { seqNo: item.seqNo };
        this.httpService.deletePermissionChangeReq(param).subscribe(data => {
          if (data.returnCode.code == "success") {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            this.query();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
        })
      }
    });
  }
  //分页
  paginate(event) {
    let rows = event.rows;
    //每页显示的条数
    this.tempImportApplyBean.pageSize = rows;
    //当前页
    let currentPage = event.page + 1;
    this.tempImportApplyBean.pageNum = currentPage;
    this.first = event.rows * event.page;

    //调用查询的方法
    this.query();
  }
}
